import React from 'react';
import './aboutme.css';


export default function AboutMe() {


    return (
        <div className="aboutme-container">
            <div className="aboutme-content">
                <div className="aboutme-title">
                    <h4>O mnie</h4>
                </div>
                <div className="aboutme-text">
                    <p>Jestem absolwentem informatyki na Uniwersytecie Mikołaja Kopernika w Toruniu. 
                        Niedawno obroniłem tytuł inżyniera informatyki pracą opartą na uczeniu maszynowym oraz splotowych sieciach neuronowych. 
                        Obecnie staram się o tytuł magistra na tym samym uniwersytecie. 
                        Przez technikum i studia ciągnęły się za mną najróżniejsze języki programowania, 
                        jednak najbardziej zainteresowała mnie sfera WebApp i MobileApp developmentu. 
                        Swoją przygodę z aplikacjami zacząłem w 2019 roku tworząc, w oparciu o React.js, projekt zespołowy JudiBell,
                        który zajął drugie miejsce w rankingu pod względem potencjału biznesowego. Obecnie rozwijam swoje umiejętności we Flutter, 
                        GOlang oraz framework'ach Django, Spring oraz Node.js. </p>
                </div>
            </div>
        </div>
    )
}