import React from 'react';
import './skills.css';
import flutterLogo from '../../assets/flutter_logo.png';
// import nodejsLogo from '../../assets/nodejs_logo.png';
import flaskLogo from '../../assets/flask_logo.png';
import springLogo from '../../assets/spring_logo.png';
import djangoLogo from '../../assets/django_logo.jpg';
import reactLogo from '../../assets/react_logo.png';
import tfLogo from '../../assets/tensorflow_logo.png';
import pygameLogo from '../../assets/pygame_logo.png';
import golangLogo from '../../assets/golang_logo.png';

export default function Skills() {



    let skills = [
        {
            'name': 'Flutter',
            'status': 'Praca',
            'src': flutterLogo,
            'url': 'https://flutter.dev/',
        },
        {
            'name': 'GoLang',
            'status': 'Praca',
            'src': golangLogo,
            'url': 'https://golang.org/',
        },
        {
            'name': 'Flask',
            'status': 'Praca',
            'src': flaskLogo,
            'url': 'https://flask.palletsprojects.com/',
        },
        {
            'name': 'Spring Boot',
            'status': 'Praca',
            'src': springLogo,
            'url': 'https://spring.io/projects/spring-boot',
        },
        {
            'name': 'Django',
            'status': 'Znajomość',
            'src': djangoLogo,
            'url': 'https://www.djangoproject.com/',
        },
        {
            'name': 'React',
            'status': 'Znajomość',
            'src': reactLogo,
            'url': 'https://reactjs.org/'
        },
        {
            'name': 'Tensorflow',
            'status': 'Hobby',
            'src': tfLogo,
            'url': 'https://www.tensorflow.org/',
        },
        {
            'name': 'Pygame',
            'status' : 'Hobby',
            'src': pygameLogo,
            'url': 'https://www.pygame.org/'
        }
    ];

    return (
        <div className="skills-container">
            <div className="skills-content">
                <div className="skills-title">
                    <h4>Umiejętności</h4>
                </div>
                <div className="skills-pills-container">
                    {skills.map((value, i) => {
                        return <div className="skills-pill-container">
                            <a href={value['url']} target="_blank" rel="noopener noreferrer">
                            <img alt={value['name']} src={value['src']}/>
                            </a>
                            <div>
                                {value['name']}
                            </div>
                            <div>
                                {value['status']}
                                </div>
                        </div>
                    })}
                </div>
            </div>
        </div>
    )
}